import React, { useState } from 'react'

import 'react-select-search/style.css'
import { Chart } from './chart'
import { Selector } from './Selector'
import { SelectedCar } from './types'

function Comparison() {
  document.title = 'Katsastustilastot | Vertaile katsastustilastoja'

  const [right, setRight] = useState<SelectedCar>({
    brand: undefined,
    model: undefined,
    year: undefined,
  })

  const [left, setLeft] = useState<SelectedCar>({
    brand: undefined,
    model: undefined,
    year: undefined,
  })

  const [showInstructions, setShowInstructions] = useState(false)

  return (
    <div className="comparison">
      <h1>Vertailu</h1>
      <span onClick={() => setShowInstructions(!showInstructions)}>
        {showInstructions ? 'Piilota ohjeet' : 'Nyt ohjeet'}
      </span>
      {showInstructions && (
        <div className="instructions">
          <p>
            Valitse vertaltavat automerkit. Voit mys tarkentaa valitsemalla
            tietyn mallin ja tietyn kyttnottovuoden.
          </p>
          <p>
            Kuvaaja nytt keskimrisen auton hylkysprosentin kehittymisen
            ajokilometrien kertyess (tumma viiva). Lisksi on kuvattu paras ja
            huonoin neljnnes (tumma katkoviiva).
          </p>
          <p>
            Vertailtavat autot nytetn kuvaajassa pistein. Mikli piste on
            tumman viivan alapuolella, on valittu auto keskimrist autoa
            parempi ja vastaavasti viivan ylpuolella olevat autot vikaantuvat
            keskimrist useammin.
          </p>
        </div>
      )}
      <div className="selectors">
        <Selector
          brand={left.brand}
          model={left.model}
          year={left.year}
          onChange={(val) => setLeft(val)}
        />
        <p>vs.</p>
        <Selector
          brand={right.brand}
          model={right.model}
          year={right.year}
          onChange={(val) => setRight(val)}
        />
      </div>
      {(left.model || right.model) && (
        <Chart
          left={left.model ? { model: left.model, year: left.year } : undefined}
          right={
            right.model ? { model: right.model, year: right.year } : undefined
          }
        />
      )}
    </div>
  )
}

export default Comparison
